$tile-spacing: var(--tile-spacing)

.tile
  align-items: stretch
  display: block
  flex-basis: 0
  flex-grow: 1
  flex-shrink: 1
  min-height: min-content
  // Modifiers
  &.is-ancestor
    margin-left: calc(#{$tile-spacing} * -1)
    margin-right: calc(#{$tile-spacing} * -1)
    margin-top: calc(#{$tile-spacing} * -1)
    &:last-child
      margin-bottom: calc(#{$tile-spacing} * -1)
    &:not(:last-child)
      margin-bottom: $tile-spacing
  &.is-child
    margin: 0 !important
  &.is-parent
    padding: $tile-spacing
  &.is-vertical
    flex-direction: column
    & > .tile.is-child:not(:last-child)
      margin-bottom: 1.5rem !important
  // Responsiveness
  +tablet
    &:not(.is-child)
      display: flex
    @for $i from 1 through 12
      &.is-#{$i}
        flex: none
        width: (divide($i, 12)) * 100%
